import React, { useEffect, useState } from "react";
import { Sidebar, Menu, MenuItem, SubMenu } from "react-pro-sidebar";
import "../../css/sidebar.css";
import { useAuth } from "../../features/AuthProvider ";
import CONSTANTS from "../utils/Constants";
import axios from "axios";
import { useNavigate } from "react-router-dom";

function SidebarComponent({ collapsed }) {
  const [data, setData] = useState([]);
  const { authToken } = useAuth();
  const history = useNavigate();
  function fetchLoggedInUsersAccess() {
    axios.post(CONSTANTS.BASE_URL + "/api/auth/get-user-access", {}, {
      headers: {
        "Authorization": "Bearer " + authToken,
      }
    })
      .then((result) => {
        console.log("result:" + JSON.stringify(result.data));
        setData(result.data);
      }).catch((e) => {
        console.log("error:" + e);
      })
  }
  useEffect(() => {
    fetchLoggedInUsersAccess();
  }, [])

  function goToRequestMapping(subModule) {
    history('/' + subModule.requestMapping);
  }

  const generateMenuItems = (submodules) => {
    return submodules.map((subModule) => (
      <MenuItem key={subModule.subModuleId}
        icon={<i className={`${subModule.icon}`}></i>}
        onClick={() => history('/' + subModule.requestMapping)}
      >{subModule.subModuleName}</MenuItem>
    ));
  };
  const generateMenu = (data) => {
    return data.map((parentSubModule) => {
      console.log(parentSubModule.subModule.length + " submodule:" + JSON.stringify(parentSubModule));
      return (
        parentSubModule.subModule.length !== 0 ? <SubMenu
          key={parentSubModule.subModuleId}
          icon={<i className={`${parentSubModule.icon}`}></i>}
          label={parentSubModule.subModuleName}
        >
          {generateMenuItems(parentSubModule.subModule)}
        </SubMenu> : parentSubModule.subModule.length == 0 && parentSubModule.parentId == null ? <MenuItem key={parentSubModule.subModuleId}
          icon={<i className={`${parentSubModule.icon}`}></i>}
          onClick={() => history('/' + parentSubModule.requestMapping)}
        >{parentSubModule.subModuleName}</MenuItem> : <></>
      );
    });
  };

  return (
    <Sidebar collapsed={collapsed} className="mt-1">
      <Menu>
        {generateMenu(data)}
      </Menu>
    </Sidebar>
  );
}

export default SidebarComponent;