import { NavLink } from "react-router-dom";
import Card from "./Card";
import CardGroup from "./CardGroup";
import Carousal from "./Carousal";
import CommonScreen from "../../Structure/CommonScreen";
import Footer from "../../Structure/Footer";
import { useAuth } from "../../../features/AuthProvider ";
import { ToastContainer, toast } from 'react-toastify';
import { useEffect } from "react";
export default function HomePage() {
    const { authToken } = useAuth(); // Access the authToken from context
    console.log("jwt-token:" + authToken);
    useEffect(() => {
        toast.success("Welcome!");
    }, [])
    return (
        <>
            <div style={{ backgroundColor: "#EEEEEE" }}>
                {/* Carousel Section */}

                <div
                    className="container"
                    style={{
                        display: "flex",
                        flexDirection: "row",
                        justifyContent: "center",
                    }}
                >
                    <div className="row">
                        {/* <h4>{t('welcome')}</h4>
                            <h5>{t('message')}</h5> */}
                    </div>
                    <Carousal />
                </div>

                {/* Reusable Sections for Products */}
                {[
                    { title: "Trending", link: "/allproducts", category: "trending", number: 3 },
                    { title: "Electronics", link: "/allproducts", category: "electronics", number: 4 },
                    { title: "New Releases", link: "/allproducts", category: "trending", number: 3 },
                    { title: "Clothing", link: "/allproducts", category: "clothes", number: 4 },
                    { title: "Groceries", link: "/allproducts", category: "groceries", number: 4 },
                    { title: "Gifts", link: "/allproducts", category: "gifts", number: 3 },
                    { title: "Accessories", link: "/allproducts", category: "accessories", number: 2 },
                    { title: "Beauty Products", link: "/allproducts", category: "beauty-products", number: 3 },
                    { title: "Books", link: "/allproducts", category: "books", number: 4 },
                    { title: "Bikes", link: "/allproducts", category: "bikes", number: 4 },
                    { title: "Kitchen Accessories", link: "/allproducts", category: "kitchen-accessories", number: 5 },
                ].map((section, index) => (
                    <div className="row" key={index}>
                        <div className="row">
                            <h4>
                                {section.title}{" "}
                                <NavLink
                                    style={{ color: "blue", font: "small-caption" }}
                                    to={section.link + "?category=" + section.category}
                                >
                                    See All
                                </NavLink>
                            </h4>
                        </div>
                        <CardGroup number={section.number} />
                    </div>
                ))}
                <ToastContainer />
            </div>
        </>
    )
}