import React from "react";
import { Navbar, Nav, NavDropdown, Container } from "react-bootstrap";
import { NavLink, useLocation, useNavigate } from "react-router-dom";
import SearchBar from "./SearchBar";
import { useHistory } from "react-router-dom";
import { useAuth } from "../../features/AuthProvider ";
import useCommonEffect from "../Session/useCommonEffect";

function NavbarComponent({ toggleSidebar, isFullscreen, toggleFullscreen }) {
  const { logout } = useAuth(); // Access the logout function
  const history = useNavigate();
  useCommonEffect();

  function moveToHome() {
    history('/home');
  }
  function logOut() {
    logout();
    history("/");
  }
  function goToProfile() {
    history("/profile")
  }
  function goToCart() {
    history("/cart/view")
  }

  return (
    <Navbar className="p-0 m-0" bg="dark" variant="dark" expand="lg" sticky="top">
      <Container>
        <Navbar.Brand href="#home" className="col-sm-1">
          <button
            className="btn btn-light"
            style={{ color: "black" }}
            onClick={toggleSidebar}
          >
            <i className="fa fa-bars" aria-hidden="true"></i>
          </button>
        </Navbar.Brand>

        {/* Navbar Links */}
        <Navbar.Toggle aria-controls="basic-navbar-nav" style={{ display: 'none' }} />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto" style={{ marginLeft: '4rem' }}>
            <Nav.Link onClick={moveToHome}>Home</Nav.Link>
            <Nav.Link href="#about">About</Nav.Link>
            <NavDropdown title="Services" id="basic-nav-dropdown">
              <NavDropdown.Item href="#web">Web Development</NavDropdown.Item>
              <NavDropdown.Item href="#seo">SEO Optimization</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#custom">Custom Solutions</NavDropdown.Item>
            </NavDropdown>
          </Nav>
          <Nav>
            <SearchBar />
          </Nav>
        </Navbar.Collapse>

        <Navbar.Toggle aria-controls="basic-navbar-nav" style={{ display: 'none' }} />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto" style={{ marginLeft: '1rem' }}>
            <NavDropdown title={<i class="ri-global-line"></i>} id="basic-nav-dropdown">
              <NavDropdown.Item href="#web">English</NavDropdown.Item>
              <NavDropdown.Item href="#seo">French</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#custom">Custom Solutions</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>

        {/* Fullscreen Toggle */}
        <button
          onClick={toggleFullscreen}
          style={{
            background: "none",
            border: "none",
            color: "white",
            fontSize: "1.2rem",
            cursor: "pointer",
            marginLeft: '1rem',
          }}
          title={isFullscreen ? "Exit Fullscreen" : "Enter Fullscreen"}
        >
          {isFullscreen ? (
            <i className="fa fa-compress" aria-hidden="true"></i>
          ) : (
            <i className="fa fa-expand" aria-hidden="true"></i>
          )}
        </button>
        <button onClick={goToCart} className="btn btn-light mx-2" style={{ marginLeft: '1rem' }}>
          <i class="ri-shopping-cart-line p-2 mx-1" style={{ border: '1px', borderRadius: '1rem' }}><span className="mx-1" style={{ borderColor: 'gray', borderRadius: '50%' }}>{1}</span></i>
        </button>
        <Nav className="ms-auto">
          <NavDropdown
            title={
              <img
                src={require("../../assets/images/user.png")}
                alt="Profile"
                style={{
                  borderRadius: "50%",
                  width: "40px",
                  height: "40px",
                  objectFit: "cover",
                }}
              />
            }
            id="profile-dropdown"
            align="end"
          >
            <NavDropdown.Item onClick={goToProfile}>My Profile</NavDropdown.Item>
            <NavDropdown.Item href="#settings">Settings</NavDropdown.Item>
            <NavDropdown.Divider />
            <NavDropdown.Item onClick={logOut}>Log out</NavDropdown.Item>
          </NavDropdown>
        </Nav>
      </Container>
    </Navbar>
  );
}

export default NavbarComponent;