import CommonScreen from "../../Structure/CommonScreen"
import StarRating from "./StarRating"
import p1 from "../../../assets/images/products/p1.jpg";
import p2 from "../../../assets/images/products/p2.jpg";
import p3 from "../../../assets/images/products/p3.jpg";
import p4 from "../../../assets/images/products/p4.jpg";
import { useState } from "react";

export default function ProductView() {

    const [selectedImage, setSelectedImage] = useState(p3); // Initial image for the second div

    const products = [
        { id: 1, src: p1, alt: "Product 1", href: "#list-item-1" },
        { id: 2, src: p2, alt: "Product 2", href: "#list-item-2" },
        { id: 3, src: p3, alt: "Product 3", href: "#list-item-3" },
        { id: 4, src: p4, alt: "Product 4", href: "#list-item-4" },
    ];

    const handleImageClick = (index) => {
        setSelectedImage(products[index].src); // Update the selected image
    };

    return (
        <>
            <div style={{ backgroundColor: "#EEEEEE" }}>
                {/* Carousel Section */}

                <div className="row">
                    <div className="col-lg-12">
                        <div className="card card-body">
                            <div className="row">
                                <div id="list-example" className="list-group col-md-2 col-sm-12">
                                    {products.map((product, index) => (
                                        <a
                                            key={product.id}
                                            className="list-group-item list-group-item-action mt-2 mb-2 col-md-12 col-sm-3"
                                            href={product.href}
                                            style={{
                                                display: "flex",
                                                flexDirection: "row",
                                                justifyContent: "center",
                                            }}
                                        >
                                            <img
                                                className="img-fluid"
                                                src={product.src}
                                                alt={product.alt}
                                                onClick={() => handleImageClick(index)} // Add click event
                                                style={{
                                                    width: "5rem",
                                                    height: "5rem",
                                                    cursor: "pointer", // Makes it clear the image is clickable
                                                }}
                                            />
                                        </a>
                                    ))}
                                </div>
                                <div className="col-md-4 col-sm-12">
                                    <div
                                        data-bs-spy="scroll"
                                        data-bs-target="#list-example"
                                        data-bs-offset="0"
                                        className="scrollspy-example"
                                        tabIndex="0"
                                    >
                                        <img
                                            className="img-fluid"
                                            src={selectedImage} // Show the selected image here
                                            alt="Selected"
                                            style={{
                                                width: "600px",
                                                height: "500px",
                                                objectFit: "cover",
                                            }}
                                        />
                                    </div>
                                </div>
                                <div className="col-md-6 col-sm-12">
                                    <div className="row" style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
                                        <div className="col-md-3 col-sm-12">
                                            <h4>Product Name</h4>
                                        </div>
                                        <div className="col-md-3 col-sm-12">
                                            <StarRating />
                                        </div>
                                        <div className="col-md-3 col-sm-12">
                                            Product Desc
                                        </div>
                                        <div className="col-md-3 col-sm-12" style={{ fontSize: '0.9rem' }}>
                                            10k bought this month
                                        </div>
                                        <div className="col-md-3 col-sm-12" style={{ fontSize: '1.4rem' }}>
                                            <div className="price-container">
                                                <span className="rupee"></span>
                                                <span className="value">1,000</span>
                                            </div>

                                        </div>
                                        <div className="col-md-3 col-sm-12">
                                            Product Specifications
                                        </div>
                                        <div className="col-sm-3 col-sm-12 card-body" style={{
                                            display: 'flex', flexDirection: 'row',
                                            justifyContent: 'center'
                                        }}>
                                            <a href="#" className="btn btn-success mx-1">
                                                <i className="ri-wallet-line"></i> Buy
                                            </a>
                                            <a href="#" className="btn btn-secondary mx-1">
                                                <i className="ri-shopping-cart-line"></i> Add to Cart
                                            </a>
                                        </div>
                                    </div>
                                </div>

                            </div>

                        </div>


                    </div>
                </div>
            </div>
        </>
    )
}