import CommonScreen from "../../../Structure/CommonScreen";

export default function ProductMaster() {

    return (
        <>
            <div className="card card-body">
                <div className="row">
                    <div className="row">
                        <h4>Product Information</h4>
                    </div>
                    <div className="col-sm-3">
                        <label className="form-label">Product Name</label>
                        <div className="">
                            <input type="text" className="form-control" id="productName" placeholder="Enter product name" />
                        </div>

                    </div>
                    <div className="col-sm-3">
                        <label className="form-label">Product description</label>
                        <div className="">
                            <input type="text" className="form-control" id="productName" placeholder="Enter product description" />
                        </div>
                    </div>
                    <div className="col-sm-3">
                        <label className="form-label">Product cost</label>
                        <div className="">
                            <input type="text" className="form-control" id="productName" placeholder="Enter product cost" />
                        </div>
                    </div>
                    <div className="col-sm-3">
                        <label className="form-label">Min price</label>
                        <div className="">
                            <input type="text" className="form-control" id="productName" placeholder="Enter product's min cost" />
                        </div>
                    </div>
                    <div className="col-sm-3">
                        <label className="form-label">Max price</label>
                        <div className="">
                            <input type="text" className="form-control" id="productName" placeholder="Enter product's max cost" />
                        </div>
                    </div>
                    <div className="col-sm-3">
                        <label className="form-label">Current discount</label>
                        <div className="">
                            <input type="text" className="form-control" id="productName" placeholder="Enter product's current discount" />
                        </div>
                    </div>
                    <div className="col-sm-3">
                        <label className="form-label">Important Information</label>
                        <div className="">
                            <textarea className="form-control" placeholder="Type something.."></textarea>
                        </div>
                    </div>
                    <div className="col-sm-3">
                        <label className="form-label">Product status</label>
                        <div className="">
                            <input type="checkbox" className="form-check-input" id="productName" />
                        </div>
                    </div>

                </div>
                <div className="row">
                    <h4>Technical Information</h4>
                </div>
                <div className="row">
                    <div className="col-sm-3">
                        <label className="form-label">
                            Manufacturer Name
                        </label>
                        <div className="">
                            <input type="text" className="form-control" placeholder="Enter manufacturer name" />
                        </div>
                    </div>
                    <div className="col-sm-3">
                        <label className="form-label">
                            Country of origin
                        </label>
                        <div className="">
                            <select className="form-control">
                                <option value="0">--Please Select--</option>
                            </select>
                        </div>
                    </div>
                    <div className="col-sm-3">
                        <label className="form-label">
                            Item part number
                        </label>
                        <div className="">
                            <input type="text" className="form-control" placeholder="Enter Item part number" />
                        </div>
                    </div>
                    <div className="col-sm-3">
                        <label className="form-label">
                            Product diamensions
                        </label>
                        <div className="">
                            <input type="text" className="form-control" placeholder="Enter product diamensions" />
                        </div>
                    </div>
                    <div className="col-sm-3">
                        <label className="form-label">
                            Net Quantity
                        </label>
                        <div className="">
                            <input type="text" className="form-control" placeholder="Enter net quantity" />
                        </div>
                    </div>
                    <div className="row mt-4" style={{ display: 'flex', flexDirection: 'row', justifyContent: 'right' }}>
                        <div className="col-md-3 col-sm-12" style={{ display: 'flex', justifyContent: 'right' }}>
                            <button className="btn btn-primary"><i class="fas fa-save mx-1"></i> Save</button>
                        </div>
                    </div>
                </div>
            </div>
            <div className="card card-body mt-3">
                <div className="row">
                    Product Master Table
                </div>
            </div>
        </>
    )
}