import { useRef } from "react";
import CommonScreen from "../../Structure/CommonScreen";
import axios from "axios";
import CONSTANTS from "../../utils/Constants";
import { useAuth } from "../../../features/AuthProvider ";
import { toast, ToastContainer } from "react-toastify";

export default function MenuTypeMaster() {
    const menuTypeId = useRef();
    const menuTypeName = useRef();
    const { authToken } = useAuth();

    function saveMenuType() {
        console.log("saving");
        var obj = {};
        obj.menuTypeId = menuTypeId.current.value;
        obj.menuTypeName = menuTypeName.current.value;
        console.log(obj);
        axios.post(CONSTANTS.BASE_URL + "/api-data/menu-type/save", obj, {
            headers: {
                "Content-Type": "application/json",
                "Authorization": "Bearer " + authToken,
            }
        }).then((result) => {
            toast.success(result.data);
        }).catch((e) => {
            toast.error("Error occurred");
        })
    }

    return (
        <>
            <div className="container mt-5">
                <div className="row justify-content-center">

                    <div className="col-lg-8 mt-2 mb-4">
                        <div className="card shadow p-4">
                            <div className="row">
                                <div className="row">
                                    <h3>Menu Type Master</h3>
                                </div>
                                <div className="col-sm-7 mt-3">
                                    <label className="form-lable">Menu Type name</label>
                                    <input type="text" className="form-control" ref={menuTypeName} placeholder="Enter menu type name" />
                                    <input type="hidden" id="menuTypeId" ref={menuTypeId} />
                                </div>
                                <div className="row mt-4" style={{ display: 'flex', flexDirection: 'row', justifyContent: 'right' }}>
                                    <div className="col-md-3 col-sm-12" style={{ display: 'flex', justifyContent: 'right' }}>
                                        <button className="btn btn-primary" onClick={saveMenuType}><i class="fas fa-save mx-1"></i> Save</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="card shadow p-4 mt-3">
                            <div className="table-responsive">
                                <table className="table table-stripped table-condensed table-borderd table-hover">
                                    <thead>
                                        <tr>
                                            <th className="text-align-center">Menu type name</th>
                                            <th className="text-align-center">Edit</th>
                                            <th className="text-align-center">Delete</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr>
                                            <th className="text-align-center">Admin</th>
                                            <th className="text-align-center"><button className="btn btn-success"><i className="fa fa-pen"></i></button></th>
                                            <th className="text-align-center"><button className="btn btn-success"><i class="ri-delete-bin-line"></i></button></th>
                                        </tr>
                                        <tr>
                                            <th className="text-align-center">Customer</th>
                                            <th className="text-align-center"><button className="btn btn-success"><i className="fa fa-pen"></i></button></th>
                                            <th className="text-align-center"><button className="btn btn-success"><i class="ri-delete-bin-line"></i></button></th>
                                        </tr>
                                        <tr>
                                            <th className="text-align-center">Vendor</th>
                                            <th className="text-align-center"><button className="btn btn-success"><i className="fa fa-pen"></i></button></th>
                                            <th className="text-align-center"><button className="btn btn-success"><i class="ri-delete-bin-line"></i></button></th>
                                        </tr>
                                        <tr>
                                            <th className="text-align-center">All</th>
                                            <th className="text-align-center"><button className="btn btn-success"><i className="fa fa-pen"></i></button></th>
                                            <th className="text-align-center"><button className="btn btn-success"><i class="ri-delete-bin-line"></i></button></th>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <ToastContainer />
        </>
    )
}